<link href="https://fonts.googleapis.com/css?family=Cormorant" rel="stylesheet">

@font-face {
  font-family: "Cormorant";
  src: url("https://fonts.googleapis.com/css?family=Cormorant")
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.outer-container {
  margin: 20px;
  padding: 10px;
  width: 400px;
  background-color: beige;
  border-radius: 3px;
  -webkit-box-shadow: 5px 5px 2px lightgrey;
          box-shadow: 5px 5px 2px lightgrey;
  border: 1px lightgrey solid;
}

.header {
  text-align: center;
  padding: 10px;
  font-size: 2em;
  font-family: "Cormorant", serif;
  font-weight: lighter;
}

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.item {
  background-color: gainsboro;
  height: 40px;
  width: 300px;
  border: 1px solid lightgrey;
  margin: 10px;
  padding: 5px;
  border-radius: 3px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.text {
  margin-left: 10px;
}

.close {
  margin-right: 10px;
}

.input-form {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
}

.input-field {

}

.add-button {

}

button {
  border-radius: 5px;
  text-shadow: 0px 1px 0px #ffffff;
  color: black;
  cursor: pointer;
  -webkit-box-shadow:inset 0px 1px 0px 0px #ffffff;
          box-shadow:inset 0px 1px 0px 0px #ffffff;
  background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #f9f9f9), color-stop(1, #e9e9e9));
  background:-webkit-linear-gradient(top, #f9f9f9 5%, #e9e9e9 100%);
  background:-o-linear-gradient(top, #f9f9f9 5%, #e9e9e9 100%);
  background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #f9f9f9), to(#e9e9e9));
  background:linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#f9f9f9', endColorstr='#e9e9e9',GradientType=0);
  border:1px solid #dcdcdc;
}

button:hover {
  background:-webkit-gradient(linear, left top, left bottom, color-stop(0.05, #e9e9e9), color-stop(1, #f9f9f9));
  background:-webkit-linear-gradient(top, #e9e9e9 5%, #f9f9f9 100%);
  background:-o-linear-gradient(top, #e9e9e9 5%, #f9f9f9 100%);
  background:-webkit-gradient(linear, left top, left bottom, color-stop(5%, #e9e9e9), to(#f9f9f9));
  background:linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
  filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#e9e9e9', endColorstr='#f9f9f9',GradientType=0);
  background-color:#e9e9e9;
}

button:active {
  position:relative;
  top:1px;
}


